.form__fieldset{
    border: none;
    color: #485665;
    border-radius: 35px;
    font-size: 20px;
    margin: 20px 10px 20px 10px;
    background-color:white;
    -webkit-box-shadow: 0px 0px 3px 0px rgba(175, 175, 175, 1);
    -moz-box-shadow: 0px 0px 3px 0px rgba(175, 175, 175, 1);
    box-shadow: 0px 0px 3px 0px rgba(175, 175, 175, 1);
    padding: 35px;
}

.form__header{
    font-size: 30px;
    font-weight: 300;
    color: #485665;
    text-align: center;
}

.form__info {
    margin-left: 40px;
    margin-bottom: 15px;
}

.form__labelText {
    max-width: 160px;
    width: 100%;
    display: inline-block;
    margin-left: 10px;
    margin-right: 20px;
}

.form__requiredInfo{
    color: red;
}

.form__currency {
    padding: 3px;
}

.form__label {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 15px;
}

@media (max-width: 500px) {
    .form__label {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }
}

.form__input {
    border-radius: 8px;
    border: 2px solid #485665;
    max-width: 250px;
    width: 100%;
}



.form__result {
    width: 100%;
    text-align: center;
}

.form__dateContent {
    font-size: 13px;
    display: flex;
    flex-direction: row-reverse;
    font-family: 'BIZ UDPMincho', serif;
}